<template>
  <div class="container">
    <div class="container-form">
      <el-form ref="qryParamRef" :model="qryParam" size="small">
        <div class="row-container">
          <div class="left-row">
            <el-row :gutter="20">
              <el-col :span="6">
                <el-form-item :label="$t('order.orderCode')" prop="custOrderNbr">
                  <el-input
                    v-model="qryParam.custOrderNbr"
                    :placeholder="inputPh('order.orderCode')"
                  ></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="6">
                <el-form-item :label="$t('order.number')" prop="qryValue">
                  <el-input
                    v-model="qryParam.qryValue"
                    placeholder="ICCID/MSISDN/IMSI"
                    maxlength="20"
                  >
                    <ct-select
                      slot="prepend"
                      v-model="qryParam.qryType"
                      :options="qryTypeOptions"
                      style="width: 100px"
                      :placeholder="selectPh('order.number')"
                      :clearable="false"
                    >
                    </ct-select>
                  </el-input>
                </el-form-item>
              </el-col>
              <el-col :span="6">
                <el-form-item :label="$t('order.busType')" prop="cmpServiceOfferId">
                  <ct-async-select
                    v-model="qryParam.cmpServiceOfferId"
                    :placeholder="selectPh('order.busType')"
                    url="/order/getServiceOfferListForQry"
                    filterable
                    method="get"
                    label-key="serviceOfferName"
                    value-key="serviceOfferId"
                  ></ct-async-select>
                </el-form-item>
              </el-col>
              <el-col :span="6">
                <el-form-item :label="$t('order.orderStatus')" prop="statusCds">
                  <ct-async-select
                    v-model="qryParam.statusCds"
                    :placeholder="selectPh('order.orderStatus')"
                    multiple
                    collapse-tags
                    url="/order/getOrderStatusListForQuery"
                    method="get"
                    label-key="attrValueName"
                    value-key="attrValue"
                  ></ct-async-select>
                </el-form-item>
              </el-col>
              <div v-show="powerSearch" class="more-query-group">
                <el-col :span="6">
                  <el-form-item :label="$t('order.serviceTime')" prop="minAcceptDate">
                    <ct-date-picker
                      :begin.sync="qryParam.minAcceptDate"
                      :end.sync="qryParam.maxAcceptDate"
                      one-month-range
                    >
                    </ct-date-picker>
                  </el-form-item>
                </el-col>
              </div>
            </el-row>
          </div>
          <div class="right-row">
            <el-form-item>
              <div class="search-button-group">
                <!-- <el-dropdown split-button type="primary" @click="qryCmpParamPage(false)"
                  >{{ $t('common.query') }}
                  <el-dropdown-menu slot="dropdown">
                    <el-dropdown-item @click.native="showUpload = true">{{
                      $t('common.batchQuery')
                    }}</el-dropdown-item>
                  </el-dropdown-menu>
                </el-dropdown> -->
                <el-button
                  type="primary"
                  icon="el-icon-search"
                  @click="qryCmpParamPage(false)"
                  >{{ $t('common.query') }}</el-button
                >
                <el-button icon="el-icon-refresh" @click="reset">{{
                  $t('common.reset')
                }}</el-button>
                <show-more-button v-model="powerSearch" />
              </div>
            </el-form-item>
          </div>
        </div>
        <!-- <div class="tool-button-group">
          <BatchExport
            :query-id-list="[1028, 1028, 1016]"
            :params="simListExportParam"
            :show-msg="tableData.length === 0"
            :export-origin="exportOrigin"
            show-prod="productInfo"
          ></BatchExport>
        </div> -->
      </el-form>
      <el-table
        ref="table"
        v-loading="loading"
        class="container-table"
        :data="tableData"
        :border="true"
        overflow-x="scroll"
        height="100%"
      >
        <el-table-column
          prop="custOrderNbr"
          :label="$t('order.orderCode')"
          show-overflow-tooltip
          width="200"
        >
          <template slot-scope="scope">
            <a class="cor-blue" @click="toDetail(scope.row)">{{
              scope.row.custOrderNbr
            }}</a>
          </template>
        </el-table-column>
        <el-table-column
          prop="useCustName"
          :label="$t('order.cust')"
          show-overflow-tooltip
          width="300"
        ></el-table-column>
        <el-table-column
          prop="iccid"
          label="ICCID"
          show-overflow-tooltip
          min-width="130"
        ></el-table-column>
        <el-table-column
          prop="imsi"
          label="IMSI"
          show-overflow-tooltip
          min-width="120"
        ></el-table-column>
        <el-table-column
          prop="accNum"
          :label="$t('order.accNum')"
          show-overflow-tooltip
          min-width="110"
        >
        </el-table-column>
        <el-table-column
          prop="statusCd"
          :label="$t('common.status')"
          show-overflow-tooltip
          min-width="110"
          :formatter="rowFormatter"
        >
        </el-table-column>
        <el-table-column
          prop="acceptDate"
          :label="$t('order.serviceTime')"
          show-overflow-tooltip
          min-width="110"
        ></el-table-column>
        <el-table-column
          prop="cmpServiceOfferId"
          :label="$t('order.busType')"
          show-overflow-tooltip
          min-width="110"
          :formatter="rowFormatter"
        ></el-table-column>
        <el-table-column
          prop="cmpCreateStaffName"
          :label="$t('order.createStaffName')"
          show-overflow-tooltip
          min-width="110"
        ></el-table-column>
      </el-table>
      <el-pagination
        v-show="tableData.length > 0"
        background
        :current-page="page.pageIndex"
        align="right"
        :page-sizes="[10, 20, 50, 100]"
        :page-size="page.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="page.total"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
      >
      </el-pagination>
    </div>
  </div>
</template>
<script>
import langMixin from '@/mixins/langMixin'
import fetch from '@/request/fetch'
import getOrderStatusListForQuery from '@/mock/modules/order/getOrderStatusListForQuery'
import getServiceOfferListForQry from '@/mock/modules/order/getServiceOfferListForQry'
export default {
  components: {},
  mixins: [langMixin],
  data() {
    return {
      powerSearch: false,
      loading: false,
      page: {
        pageIndex: 1,
        pageSize: 10,
        total: 0,
      },
      tableData: [],
      qryParam: {
        custOrderNbr: '',
        statusCds: [],
        userName: '',
        createTimeBegin: '', // 开始时间
        createTimeEnd: '', // 结束时间
        qryValue: '',
        qryType: 'iccid',
      },
      qryTypeOptions: [
        {
          value: 'iccid',
          label: 'ICCID',
        },
        {
          value: 'msisdn',
          label: 'MSISDN',
        },
        {
          value: 'imsi',
          label: 'IMSI',
        },
      ],
      pickerOptions: {
        disabledDate(time) {
          return (
            time.getTime() < Date.now() - 30 * 24 * 60 * 60 * 1000 ||
            time.getTime() > Date.now()
          )
        },
      },
    }
  },
  mounted() {
    this.qryCmpParamPage(false)
  },
  methods: {
    qryCmpParamPage(pageChangeFlag) {
      if (!pageChangeFlag) {
        this.page.pageIndex = 1
        this.page.pageSize = 10
      }
      const param = {
        ...this.qryParam,
        qryType: this.qryParam.qryValue ? this.qryParam.qryType : '',
        pageInfo: {
          ...this.page,
        },
      }
      this.loading = true
      fetch('/order/qryOrderListPage', param, { method: 'post' })
        .then((res) => {
          this.tableData = res.result || []
          this.page.total = res.rowCount || 0
        })
        .finally(() => {
          this.loading = false
        })
    },
    handleSizeChange(val) {
      this.page.pageSize = val
      this.page.pageIndex = 1
      this.qryCmpParamPage(true)
    },
    handleCurrentChange(val) {
      this.page.pageIndex = val
      this.qryCmpParamPage(true)
    },
    toDetail(row) {
      this.$router.push({ path: 'orderDetail', query: { row: JSON.stringify(row) } })
    },
    rowFormatter(row, column, val) {
      if (column.property === 'statusCd') {
        return getOrderStatusListForQuery[localStorage.getItem('language')].data.find(
          (item) => item.attrValue === val
        ).attrValueName
      } else if (column.property === 'cmpServiceOfferId') {
        return getServiceOfferListForQry[localStorage.getItem('language')].data.find(
          (item) => item.serviceOfferNbr === val
        ).serviceOfferName
      }
    },
    // 重置
    reset() {
      this.$refs.qryParamRef.resetFields()
      this.qryParam.statusCds = []
      this.qryParam.qryType = 'iccid'
      this.$nextTick(() => this.qryCmpParamPage(false))
    },
  },
}
</script>
<style lang="scss" scoped>
@import '@/assets/css/page.scss';
.action-button-group {
  display: flex;
  .el-button:first-child {
    padding-left: 0;
  }
}
</style>
